"use client"

import { useState, useEffect } from "react"
import Header from "@/components/header"
import { HeroSection } from "@/components/hero-section"
import { FilterBar } from "@/components/filter-bar"
import { KulinerGrid } from "@/components/kuliner-grid"
import { CarouselBaru } from "@/components/carousel-baru"
import { FAQSection } from "@/components/faq-section"
import Footer from "@/components/footer"
import { loadAll } from "@/lib/data"

export default function Home() {
  const [data, setData] = useState<any>(null)
  const [filters, setFilters] = useState({ kategori: [], sort: "populer" })
  const [area, setArea] = useState(null)

  useEffect(() => {
    // Load data dummy dari file JSON yang ada di public/mock
    loadAll().then(res => setData(res))
  }, [])

  if (!data) return <div className="p-10 text-center">Loading Data Nusantara...</div>

  return (
    <>
      <Header />
      <main className="bg-background min-h-screen">
        <HeroSection 
          kategori={data.kategori} 
          kota={Object.values(data.provKota).flat() as string[]} 
          onSearch={(q) => console.log("Cari:", q)} 
        />
        
        <FilterBar 
          kategori={data.kategori} 
          onFilterChange={(f) => setFilters(f as any)} 
        />

        <KulinerGrid 
          kuliner={data.kuliner} 
          popular={data.popular} 
          area={area}
          filters={filters as any}
        />

        <CarouselBaru baru={data.kuliner.slice(0, 5)} />
        
        <FAQSection />
      </main>
      <Footer />
    </>
  )
}